body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("https://preview.redd.it/d9spuwer2c491.png?width=1050&format=png&auto=webp&v=enabled&s=f6b6908ea6b25b4914c6aadce02a54ed064f4ae9");
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.Rock {
  background-color: rgb(138, 117, 117);
}
.Ghost {
  background-color: rgb(247, 247, 247);
}
.Electric {
  background-color: rgb(255, 255, 161);
}
.Bug {
  background-color: #f6d6a7;
}
.Poison {
  background-color: #e0a7f6;
}
.Normal {
  background-color: #f4f4f4;
}
.Fairy {
  background-color: rgba(255, 192, 203, 0.863);
}
.Fire {
  background-color: #fbe3df;
}
.Grass {
  background-color: #e2f9e1;
}
.Water {
  background-color: #96d3ff;
}
.Ground {
  background-color: burlywood;
}
.Fighting {
  background-color: rgb(201, 61, 61);
}
.Dragon {
  background-color: rgb(84, 159, 249);
}
.Ice {
  background-color: rgb(196, 240, 255);
}
.Psychic {
  background-color: rgb(243, 93, 118);
}
.Flying {
  background-color: #f5f5f5;
}
