.type-container #Grass,
#Poison,
#Fire,
#Water,
#Flying,
#Bug,
#Fighting,
#Psychic,
#Fairy,
#Dragon,
#Ice,
#Rock,
#Ground,
#Normal,
#Ghost,
#Electric {
  border-radius: 1rem;
  padding: 0.2rem 0.4rem;
  background-color: rgba(255, 255, 255, 0.3);
}
.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 3rem 0.5rem;
}
.all-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.pokemon-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 0;
  margin: 0.3rem;
  border: 1px solid #efefef;
  border-radius: 0.2rem;
  border: none;
  min-width: 160px;
  text-align: center;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.089);
}
h1 {
  color: whitesmoke;
  font-size: 75px;
}
h3 {
  margin-bottom: 0.2rem;
  font-size: 20px;
  text-decoration: overline;
}
.pokemon-container .number {
  border-radius: 1rem;
  padding: 0.2rem 0.4rem;
  background-color: rgba(255, 255, 255, 0.3);
}
.pokemon-container img {
  width: 120px;
  height: 120px;
}
.pokemon-container small {
  text-transform: capitalize;
}
.detail-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.detail-wrapper button {
  color: rgb(22, 22, 22);
  padding: 0.5rem;
  margin-top: 1rem;
  border: none;
  border-radius: 0.2rem;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.185);
}
.load-more {
  background: #f5f5f5 linear-gradient(#f5f5f5, #f1f1f1) repeat-x;
  border-radius: 2px;
  border: 1px solid #c6c6c6;
  color: #444;
  padding: 0.5rem 1.5rem;
  min-width: 50%;
  margin-top: 1rem;
}
.detail-wrapper .Fire,
.Water,
.Rock,
.Poison,
.Rock,
.Ground,
.Psychic,
.Fighting,
.Fairy,
.Bug,
.Electric,
.Flying,
.Dragon,
.Normal,
.Ice,
.Ghost,
.Grass,
.Fire {
  border-radius: 1rem;
  padding: 0.2rem 0.4rem;
  /* background-color: rgba(255, 255, 255, 0.3); */
}
label {
  color: whitesmoke;
  margin-left: 20px;
  font-weight: bold;
  font-size: larger;
}
